<template>
    <div class="box">
        <div class="content">
            <div class="current-grade">
                <div><img :src="require(`@/assets/img/upLv0${origin}.png`)" /></div>
                <div>
                    <span>{{$t('currentLevel')}}</span>
                    <div>
                        <!-- <span v-text="`V${origin <= 0 ? 'IP' : origin}`"></span> -->
                        <span v-text="`V${origin || 'IP'}`"></span>
                        <img src="@/assets/img/upLv00.png" />
                        <img :src="require(`@/assets/img/upLv01${origin < 1 ? '-none' : ''}.png`)" />
                        <img :src="require(`@/assets/img/upLv02${origin < 2 ? '-none' : ''}.png`)" />
                        <img :src="require(`@/assets/img/upLv03${origin < 3 ? '-none' : ''}.png`)" />
                        <img :src="require(`@/assets/img/upLv04${origin < 4 ? '-none' : ''}.png`)" />
                        <img :src="require(`@/assets/img/upLv05${origin < 5 ? '-none' : ''}.png`)" />
                    </div>
                </div>
            </div>
            <slot />
            <div class="privileges" v-if="origin >= 2">
                <div class="title">
                    <span>{{$t('LightUpPrivileges')}}</span>
                    <span>V1</span>
                </div>
                <div class="count">
                    <div>
                        <img v-for="(i,k) in v1Total" :key="k" :src="require(`@/assets/img/upLv01${i <= v1Count ? '' : '-not'}.png`)" />
                    </div>
                    <p>{{ v1Count }}<span>/{{ v1Total }}</span></p>
                </div>
                <textarea v-model="v1Address" maxlength="44" :placeholder="$t('EnterFriendWallet')" />
                <Button :disabled="v1Count === 0" @click="share(1)" color="linear-gradient(41deg, #F5D755, #FFA271)">{{$t('LightUpPrivileges')}}</Button>
            </div>
            <div class="privileges" v-if="origin >= 3">
                <div class="title">
                    <span>{{$t('LightUpPrivileges')}}</span>
                    <span>V2</span>
                </div>
                <div class="count">
                    <div>
                        <img v-for="(i,k) in v2Total" :key="k" :src="require(`@/assets/img/upLv02${i <= v2Count ? '' : '-not'}.png`)" />
                    </div>
                    <p>{{ v2Count }}<span>/{{ v2Total }}</span></p>
                </div>
                <textarea v-model="v2Address" maxlength="44" :placeholder="$t('EnterFriendWallet')" />
                <Button :disabled="v2Count === 0" @click="share(2)" color="linear-gradient(41deg, #F5D755, #FFA271)">{{$t('LightUpPrivileges')}}</Button>
            </div>
        </div>
    </div>
</template>

<script>
    import { Button } from 'vant';

    export default {
        name: 'grade-box',
        components: {
            Button,
        },
        props: {
            title: {
                type: String,
                default: '身份升级'
            },
            origin: {
                type: Number,
                default: 0
            }
        },
        computed: {
            v1Total() {
                switch (this.origin) {
                    case 2:
                        return 3;
                    case 3:
                        return 4;
                    default:
                        return 0;
                }
            },
            v2Total() {
                switch (this.origin) {
                    case 3:
                        return 3;
                    default:
                        return 0;
                }
            }
        },
        data() {
            return {
                v1Count: 0,
                v2Count: 0,
                v1Address: '',
                v2Address: '',
            }
        },
        methods: {
            async share(grade) {
                const address = grade === 1 ? this.v1Address : this.v2Address;
				const toLv = (await this.$tronWeb.call({
					contractAddress: this.$contracts.Relations.address,
				    method: 'levelOf(address)',
				    parameter: [
				        { type: 'address', value: address }
				    ]
				}).then()).constant_result[0].decode(['uint256']).toNumber()
                if (!window.tronWeb.isAddress(address)) {
                    return this.$toast.fail(this.$t('IdentityReminder01'));
                }else if(toLv >= grade){
					return this.$toast.fail(this.$t('IdentityReminder02'));
				}
				

                this.$tronWeb.call({
					contractAddress: this.$contracts.Manager.address,
                    method: 'useGiftDLevel(uint256,address)',
                    parameter: [
                        { type: 'uint256', value: grade },
                        { type: 'address', value: address }
                    ]
				}).then(result => {
                    this.$contracts.Manager.useGiftDLevel(grade, address).send({
						feeLimit: this.$store.state.generalFeeLimit.toSun() 
					}).then(res=>{
						this.$store.commit('loadingShow',true)
						
						const interval = setInterval(() => {
							window.tronWeb.trx.getTransaction(res).then(transaction => {
								if (transaction.ret && transaction.ret[0].contractRet === 'SUCCESS') {
									this.$store.commit('loadingShow',false)
									this.v1Address = '';
									this.v2Address = '';
									this.init();
									this.$toast.success(this.$t('IdentityReminder03'));
									clearInterval(interval);
								}
							})
						}, 1000);
					})
                })
            },
            init() {
                //剩余可赠送等级
                this.$tronWeb.call({
					contractAddress: this.$contracts.Manager.address,
                    method: 'giftList(address)',
                    parameter: [{ type: 'address', value: this.$address }]
				}).then(result => {
                    const [,V1, V2] = result.constant_result[0].decode(['uint256[]']);
					
					if(V1 || V2){
						this.v1Count = V1.toNumber();
						this.v2Count = V2.toNumber();
					}
                })
            }
        },
        created() {
            this.$tronWeb && this.init();
        }
    }
</script>

<style lang="scss" scoped>
    .privileges {
        padding: 28px 22px 20px;
        border-radius: 20px;
        background-color: #E8E8E8;
        box-shadow: 0px 2px 4px rgba(#000, .3);
        margin-bottom: 62px;
		&:last-child{
			margin-bottom: 0;
		}
        &>button {
            width: 100%;
            height: 90px;
            color: #B56900 !important;
            font-size: 44px;
            border-radius: 8px;
            box-shadow: 0 6px 12px rgba(#000, .16);
        }
        &>textarea {
            width: 100%;
            height: 160px;
            resize: none;
            border: none;
            font-size: 40px;
        }
        &>.count {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin: 12px 0 32px;
            &>p {
                color: #FFC533;
                font-size: 40px;
                &>span {
                    color: #656363;
                }
            }
            &>div {
                display: flex;
                align-items: center;
                &>img {
                    width: 40px;
                    height: 40px;
                    margin-right: 20px;
                }
            }
        }
        &>.title {
            color: #656363;
            font-size: 40px;
            text-align: left;
            &>span:last-child {
                color: #FFC533;
                margin-left: 20px;
            }
        }
    }
    .current-grade {
        display: flex;
        align-items: center;
        &>div {
            &:last-child {
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                color: #fff;
                font-size: 40px;
                &>div {
                    display: flex;
                    align-items: center;
                    margin-top: 8px;
                    &>img {
                        width: 40px;
                        height: 40px;
                        margin-left: 20px;
                    }
                }
            }
            &:first-child {
                width: 143px;
                height: 125px;
                background-image: url(../../assets/img/lvBg.png);
                background-size: 100% 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                margin-right: 22px;
                &>img {
                    width: 54px;
                    height: 52px;
                }
            }
        }
    }
    .content {
        padding: 30px 40px;
		margin-bottom: 0;
    }
</style>